.note {
  height: 280px;
  width: 260px;
  border-radius: 30px;
  padding: 25px;
  display: flex;
  flex-direction: column;
  background-color: blueviolet;
}

.note-txt {
  flex: 1;
  resize: none;
  font-size: 1rem;
  line-height: 3rem;
  outline: none;
  border: none;
  background-color: transparent;
}

.footer-itm {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-itm img {
  width: 18px;
  height: 20px;
  cursor: pointer;
  opacity: 0;
  transition: all 0.3s;
}

.note:hover .footer-itm img {
  opacity: 1;
}
