.sidebar {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.sidebar img {
  width: 50px;
  cursor: pointer;
}

.color-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  padding: 0px;
  height: 0;
  transition: ease-out 0.5s;
}

.color-list-active {
  height: 330px;
}

.color-list-itm {
  cursor: pointer;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  list-style: none;
}
